/* --------------------------------------------------------- BUTTONS --------------------------------------------------------- */
/* Basic Buttons */
.stx-btn {
  /* Inactive Button */
  display: inline-block;
  cursor: pointer;
  padding: 0px 8px;
  border: solid 1px #ddd;
  font-weight: bold;
  line-height: 27px;
  color: #7e7e7e;
  text-align: center;
  overflow: visible;
  background-repeat: no-repeat;
  border-radius: 3px;
  transition: color 0.25s, border 0.25s, box-shadow 0.25s;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.stx-btn:hover {
  color: #444444;
  border: solid 1px #c5c5c5;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.1), inset 0px -8px 6px rgba(100, 100, 100, 0.03);
}
.stx-btn:active {
  color: #333;
  box-shadow: inset 0px 8px 6px rgba(100, 100, 100, 0.03);
}
/* Panel Buttons */
.stx-btn-panel,
.stx-btn-panel span {
  /* Button Defaults */
  display: inline-block;
  width: 20px;
  height: 20px;
  cursor: pointer;
  transition: transform 0.2s;
}
.stx-btn-panel {
  /* Button Spacing */
  margin: 2px;
  opacity: 0;
  transition: opacity 0.5s;
  display: none;
}
.smartcharts-light .stx-btn-panel {
  background-color: #ffffff;
}
.smartcharts-dark .stx-btn-panel {
  background-color: #0e0e0e;
}
.stx-btn-panel > span {
  display: flex;
  justify-content: center;
  align-items: center;
  background: no-repeat center;
}
.stx-btn-panel:hover span, .stx-btn-panel:active span {
  /* Make buttons full opacity on hover or hit */
  opacity: 1;
}
.stx-btn-panel:active svg {
  /* Scale up when active */
  transform: scale(1.2, 1.2);
}
.stx-btn-panel.stx-show {
  display: flex;
  justify-content: center;
  align-items: center;
}
.stx-btn-panel .stx-ico-close {
  width: 16px;
  height: 16px;
}
/* Up/Down Arrow indicators */
.cq-change:after {
  float: right;
  margin-top: 5px;
  margin-right: 5px;
  content: "";
  width: 0px;
  height: 0px;
  display: block;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
}
.cq-change.up:after {
